<template>
    
    <div>
        <Button icon="bi bi-pencil text-primary" text rounded severity="info" @click="visible = true"/>

        <Dialog v-model:visible="visible" modal header="İçerik Tipi Güncelle" :style="{ width: '50vw' }" :breakpoints="{ '960px': '75vw', '641px': '95vw' }">
            <form @submit.prevent="update" class="d-flex flex-column h-100">

                <div class="row g-5 align-items-center">
                    <label class="col-lg-6 fw-bold">Adı</label>
                    <div class="col-lg-6">
                        <InputText class="w-100" v-model="form.name" />
                    </div>
                </div>

                <Divider />

                <div class="row g-5 align-items-center">
                    <label class="col-lg-6 fw-bold">Youtube & Vimeo</label>
                    <div class="col-lg-6">
                        <Dropdown v-model="form.video_type" :options="['youtube','vimeo']" placeholder="Lütfen seçiniz" class="w-100" />
                    </div>
                </div>

                <Divider />

                <div class="row g-5 align-items-center">
                    <label class="col-lg-6 fw-bold">Video ID</label>
                    <div class="col-lg-6">
                        <InputText class="w-100" v-model="form.video_id" />
                    </div>
                </div>

                <div class="separator my-10"></div>
                <button type="submit" class="btn btn-primary" :class="{ 'opacity-25': form.processing }" :disabled="form.processing">Kaydet</button>
                <FormErrors :form="form"/>

            </form>
        </Dialog>
    </div>

</template>

<script setup>

import {ref} from "vue";
import { useForm } from "@inertiajs/vue3";
import Dialog from "primevue/dialog";
import InputText from 'primevue/inputtext';
import Dropdown from "primevue/dropdown";
import Divider from 'primevue/divider';
import Button from "primevue/button";

const props = defineProps({
    video:Object,
});

const visible = ref(false);

const form = useForm({
    name:props.video.name,
    video_type:props.video.video_type,
    video_id:props.video.video_id,
});

const update = () => {

    form.put(route('videos.update',props.video),{
        preserveScroll: true,
        onSuccess: (page) => {
            visible.value = false;
        }
    });
}

</script>